html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: #0000;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  max-width: 100%;
  white-space: normal;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

@keyframes animation-fade-pulse_lightmode {
  0% {
    opacity: .3;
    background: #a99;
  }

  40% {
    opacity: 1;
    background: #a77;
  }

  80% {
    opacity: .3;
    background: #a99;
  }
}

@keyframes animation-fade-pulse_bluish {
  0% {
    opacity: .3;
    background: #99a;
  }

  40% {
    opacity: 1;
    background: #77a;
  }

  80% {
    opacity: .3;
    background: #99a;
  }
}

@keyframes animation-fade-pulse_greenish {
  0% {
    opacity: .3;
    background: #9a9;
  }

  40% {
    opacity: 1;
    background: #7a7;
  }

  80% {
    opacity: .3;
    background: #9a9;
  }
}

@keyframes animation-fade-pulse_darkmode {
  0% {
    opacity: .3;
    background: #a9a;
  }

  40% {
    opacity: 1;
    background: #a7a;
  }

  80% {
    opacity: .3;
    background: #a9a;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes slideup {
  0% {
    transform: translateY(3rem);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes turbulence {
  0% {
    filter: url("#turbulence_0");
  }

  12.5% {
    filter: url("#turbulence_1");
  }

  25% {
    filter: url("#turbulence_2");
  }

  37.5% {
    filter: url("#turbulence_3");
  }

  50% {
    filter: url("#turbulence_4");
  }

  62.5% {
    filter: url("#turbulence_5");
  }

  75% {
    filter: url("#turbulence_6");
  }

  100% {
    filter: url("#turbulence_7");
  }
}

._iconButton-module__icon_button {
  height: 3rem;
  width: 3rem;
  background-color: initial;
  cursor: pointer;
  border: none;
  padding: 0;
}

@media (hover: hover) {
  ._iconButton-module__icon_button:after {
    opacity: 0;
    content: "";
    width: calc(100% - .6rem);
    height: calc(100% - .6rem);
    border-radius: .25rem;
    transition: opacity .15s cubic-bezier(.45, .05, .55, .95);
    position: absolute;
    inset: .3rem;
    overflow: hidden;
  }

  .theme--lightmode ._iconButton-module__icon_button:after {
    color: #211;
    background: #3d3333;
  }

  .theme--transition ._iconButton-module__icon_button:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  .theme--bluish ._iconButton-module__icon_button:after {
    color: #112;
    background: #33333d;
  }

  .theme--transition ._iconButton-module__icon_button:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  .theme--greenish ._iconButton-module__icon_button:after {
    color: #121;
    background: #333d33;
  }

  .theme--transition ._iconButton-module__icon_button:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  .theme--darkmode ._iconButton-module__icon_button:after {
    color: #ede;
    background: #cccdcc;
  }

  .theme--transition ._iconButton-module__icon_button:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  ._iconButton-module__icon_button:hover:after {
    opacity: .2;
  }

  ._iconButton-module__icon_button:active:after {
    width: 100%;
    height: 100%;
    opacity: .6;
    border-radius: inherit;
    transition: none;
    inset: 0;
  }

  .theme--lightmode ._iconButton-module__icon_button:active:after {
    color: #211;
    background: #3d3333;
  }

  .theme--transition ._iconButton-module__icon_button:active:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  .theme--bluish ._iconButton-module__icon_button:active:after {
    color: #112;
    background: #33333d;
  }

  .theme--transition ._iconButton-module__icon_button:active:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  .theme--greenish ._iconButton-module__icon_button:active:after {
    color: #121;
    background: #333d33;
  }

  .theme--transition ._iconButton-module__icon_button:active:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }

  .theme--darkmode ._iconButton-module__icon_button:active:after {
    color: #ede;
    background: #cccdcc;
  }

  .theme--transition ._iconButton-module__icon_button:active:after {
    transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
  }
}

._iconButton-module__icon_button > svg {
  width: 60%;
  height: 60%;
  stroke-width: 3px;
  padding: 20%;
}

.theme--lightmode ._iconButton-module__icon_button > svg {
  stroke: #a77;
}

.theme--transition ._iconButton-module__icon_button > svg {
  transition: color .2s ease-out, background .2s ease-out, background-color .2s ease-out, border-color .2s ease-out, box-shadow .2s ease-out, text-shadow .2s ease-out, stroke .2s ease-out, filter .2s ease-out;
